<template>
  <div class="wrapper">
    <parallax class="page-header header-filter" :style="headerStyle">
      <div class="md-layout">
        <div class="md-layout-item">
          <div class="image-wrapper">
            <div class="brand">
              <h1 style="color:white;">Description</h1>
              <h3 style="color:white;">
                Information of your favourite activity and restaurant.
              </h3>
            </div>
          </div>
        </div>
      </div>
    </parallax>

    <div class="main main-raised">
      <div class="section section-basic">
        <!-- RESTAURANT CAROUSEL SECTION-->

        <div class="md-layout">
          <div class="md-layout-item md-size-66 mx-auto md-small-size-100">
            <h1 class="text-uppercase lined mb-4" style="color:palevioletred">
              Restaurant Labyrinth
              <md-button
                v-if="loggedIn"
                v-bind:class="getClass()"
                v-on:click="checkIfFav()"
                style="margin-left:10px; margin-top:10px"
                ><md-icon>favorite</md-icon></md-button
              >
            </h1>

            <md-card>
              <carousel
                :per-page="1"
                loop
                :speed="700"
                autoplay
                :autoplay-timeout="5000"
                :mouse-drag="false"
                navigationEnabled
                navigationNextLabel="<i class='material-icons'>keyboard_arrow_right</i>"
                navigationPrevLabel="<i class='material-icons'>keyboard_arrow_left</i>"
              >
                <slide>
                  <div class="carousel-caption">
                    <h4></h4>
                  </div>
                  <img src="@/assets/res-img/rs0030-1.jpg" alt="carousel1" />
                </slide>
                <slide>
                  <div class="carousel-caption">
                    <h4></h4>
                  </div>
                  <img src="@/assets/res-img/rs0030-2.jpg" alt="carousel2" />
                </slide>
              </carousel>
            </md-card>
          </div>
        </div>

        <br />

        <!-- DESCRIPTION SECTION        -->
        <div class="pb-5">
          <div class="container">
            <div class="row">
              <div class="col-lg-8">
                <h3 class="text-uppercase lined mb-4" style="color:#ff286c">
                  Restaurant Information
                </h3>
                <p class="text-muted">
                  The idea for Restaurant Labyrinth came about when Chef LG Han
                  realised that whilst Singapore is a bastion of great street
                  food, the quality of dishes vary widely from hawker centre to
                  hawker centre. Thus, Restaurant Labyrinth was conceptualized
                  to provide a consistent level of top-notch, elevated
                  Singaporean street food under one roof, complete with an edgy
                  ambience.
                </p>
                <p class="text-muted">
                  Starting with a menu featuring some of Chef LG’s most-loved
                  local dishes, diners will now be able to indulge in his
                  signature “Ang Moh” Chicken Rice, the recipe of which was left
                  to him by his beloved late grandmother, as well as Laksa
                  featuring Local Green Lipped Mussels stuffed with homemade
                  Fish Paste, Crab Meat Her Geow and Grilled Argentinian Red
                  Prawns in a luscious broth.
                </p>
              </div>
              <br />
              <div class="col-lg-4">
                <h3 class="text-uppercase lined mb-4" style="color:#ff286c">
                  Information
                </h3>
                <h4 class="h5 text-uppercase">Cuisine</h4>
                <p class="text-muted text-sm">
                  Local, Southeast Asian, Modern Asian
                </p>
                <h4 class="h5 text-uppercase">Hours</h4>
                <p class="text-muted text-sm">
                  Wed: 6:30-10:30pm (Last Seating: 8pm) <br />
                  Thu-Sun: 12-2:30pm (Last Order: 1pm), 6:30-10:30pm (Last
                  Seating: 8pm) <br />
                  Closed Monday and Tuesday
                </p>
                <h4 class="h5 text-uppercase">Good for:</h4>
                <p class="text-muted text-sm">
                  Business Meetings, Fine Dining, Drinks, Private Functions,
                  Romantic Dates
                </p>
                <h4 class="h5 text-uppercase">Location</h4>
                <p class="text-muted text-sm">
                  Marina Bay, Promenade, Esplanade
                </p>
                <h4 class="h5 text-uppercase">Address</h4>
                <p class="text-muted text-sm">
                  8 Raffles Avenue <br />
                  #02-23 <br />
                  Esplanade Mall <br />
                  Singapore (039802)<br />
                </p>

                <br />
                <h3 class="text-uppercase lined mb-4" style="color:#ff286c">
                  Favourite
                </h3>
                <md-button
                  v-if="loggedIn"
                  v-bind:class="getClass()"
                  v-on:click="checkIfFav()"
                  style="margin:auto;"
                  ><md-icon>favorite</md-icon></md-button
                >
              </div>
            </div>
          </div>
        </div>

        <br />
        <br />

        <!-- Google API -->

        <div style="width:1000px; margin:auto;">
          <div>
            <label>
              <!-- <gmap-autocomplete @place_changed="initMarker" value="Restaurant Labyrinth" readonly></gmap-autocomplete> -->
              <GmapAutocomplete
                @place_changed="setPlace"
                value="Restaurant Labyrinth Singapore"
              />
              <button @click="addMarker">
                Add
              </button>
              <!-- <button @click="addLocationMarker">Add</button> -->
            </label>
            <br />
          </div>
          <br />
          <GmapMap
            :center="center"
            :zoom="12"
            style="width:100%;  height: 400px;"
          >
            <GmapMarker
              :key="index"
              v-for="(m, index) in markers"
              :position="m.position"
              @click="center = m.position"
            />
          </GmapMap>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebaseApp from "@/firebase.js";
import { getFirestore } from "firebase/firestore";
import {
  doc,
  setDoc,
  addDoc,
  deleteDoc,
  updateDoc,
  getDoc
} from "firebase/firestore";
import { getAuth } from "firebase/auth";

const db = getFirestore(firebaseApp);

export default {
  components: {},
  name: "RS0030",
  bodyClass: "RS0030-page",
  props: {
    image: {
      type: String,
      default: require("@/assets/img/bg7.jpg")
    },

    signup: {
      type: String,
      default: require("@/assets/img/city.jpg")
    },
    landing: {
      type: String,
      default: require("@/assets/img/landing.jpg")
    },
    profile: {
      type: String,
      default: require("@/assets/img/profile.jpg")
    }
  },
  data() {
    return {
      firstname: null,
      email: null,
      password: null,
      loggedIn: false,
      liked: false,
      objectID: "",
      name: "",
      category: "",
      img: "",
      address: "",
      website: "",
      latitude: 1,
      longtitude: 1,
      center: { lat: 45.508, lng: -73.587 },
      currentPlace: null,
      markers: [],
      places: []
    };
  },
  async created() {
    const db = getFirestore(firebaseApp);
    const auth = getAuth();
    const user = auth.currentUser.email;
    const item = doc(db, "wander-food", "Restaurant Labyrinth");
    const querySnapshot = await getDoc(item);
    this.objectID = querySnapshot.data().objectID;
    this.name = querySnapshot.data().restaurantname;
    this.category = querySnapshot.data().category;
    this.img = querySnapshot.data().image;
    this.address = querySnapshot.data().address;
    this.website = querySnapshot.data().website;
    this.latitude = querySnapshot.data().latitude;
    this.longtitude = querySnapshot.data().longtitude;

    if (user) {
      this.loggedIn = true;
      const docRef = doc(db, "users", user, "wishlist", this.name);
      const docSnap = await getDoc(docRef);
      console.log(docSnap.exists());
      if (docSnap.exists()) {
        this.liked = true;
      } else {
        this.liked = false;
      }
    }
  },
  methods: {
    leafActive() {
      if (window.innerWidth < 768) {
        this.leafShow = false;
      } else {
        this.leafShow = true;
      }
    },

    setPlace(place) {
      this.currentPlace = place;
    },
    addMarker() {
      if (this.currentPlace) {
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        };
        this.markers.push({ position: marker });
        this.places.push(this.currentPlace);
        this.center = marker;
        this.currentPlace = null;
      }
    },
    geolocate: function() {
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    },
    getClass() {
      return {
        "md-primary md-just-icon md-round": this.liked,
        "md-just-icon md-round": !this.liked
      };
    },
    checkIfFav() {
      if (this.liked) {
        this.removeFromFav();
      } else {
        this.addToFav();
      }
    },
    async addToFav() {
      this.liked = true;
      try {
        const auth = getAuth();
        const user = auth.currentUser.email;
        await setDoc(doc(db, "users", user, "wishlist", this.name), {
          objectID: this.objectID,
          name: this.name,
          category: this.category,
          image: this.img,
          address: this.address,
          website: this.website,
          latitude: this.latitude,
          longtitude: this.longtitude
        });
      } catch (error) {
        console.error("Error adding document: ", error);
      }
    },
    async removeFromFav() {
      this.liked = false;
      try {
        const auth = getAuth();
        const user = auth.currentUser.email;
        await deleteDoc(doc(db, "users", user, "wishlist", this.name));
      } catch (error) {
        console.error("Error adding document: ", error);
      }
    }
  },
  computed: {
    headerStyle() {
      return {
        backgroundImage: `url(${this.image})`
      };
    },
    signupImage() {
      return {
        backgroundImage: `url(${this.signup})`
      };
    }
  },
  mounted() {
    this.leafActive();
    window.addEventListener("resize", this.leafActive);
    this.geolocate();
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.leafActive);
  }
};
</script>

